@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Recursive:wght@300;500;600;700;800;900&display=swap);
body {
  box-sizing: border-box;
  font-family: "Inconsolata", monospace;
}

code {
  font-family: "Inconsolata", monospace;
}

button {
  font-family: "Inconsolata", monospace;
  font-weight: bolder;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

html {
  font-size: 10px;
  font-family: "Inconsolata", monospace;
  font-weight: 400;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

/* EDIT BELOW */
/* find typical screen sizes to add, if not basic edits */
@media screen and (min-width: 1024px) {
  aside,
  main {
    width: 50%;
  }

  footer {
    height: 300px;
  }
}

@media screen and (min-width: 1224px) {
  aside {
    width: 30%;
  }
  main {
    width: 70%;
  }
}

.App {
  height: 100vh;
  width: 100vw;
  color: #f9f6f2;
  background-color: #151010;

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

div.container {
  display: flex;
  flex-direction: column;

  height: 65vmin;
  width: 100vmin;
  margin: 0px 2.5rem;
  padding: 0.5rem 0px;
  border: 0.3vmin solid #f9f6f2;
  border-bottom: 4px solid #151010;
  border-right: 4px solid #151010;

  box-shadow: 0.8vmin 0.8vmin 0px 0px #f9f6f2;
}

div.container > * {
  display: flex;
}

div.container > div#LeftRight {
  height: 100%;
  width: 100%;
}

.App > button {
  height: 50px;
  width: 120px;
  border: 3px solid #01e9d9;
  background-color: transparent;
  color: #f9f6f2;
  font-size: 1.3rem;
  font-weight: 100;
  text-transform: uppercase;
}

.Searchbar {
  /* border: 1px solid #f9f6f2; */
  justify-content: center;
  height: 10%;
  width: 99%;
}

#search-container {
  display: flex;
  align-items: center;
  margin: 1rem;
  border-bottom: 1px solid #f9f6f2;
}

#search-container > img {
  margin-right: 1vmin;
  margin-bottom: 1vmin;
}

#search-container > input[type="text"] {
  height: 4vmin;
  width: 40vmin;
  border: none;
  background-color: transparent;
  color: #f9f6f2;
  font-family: "Inconsolata", monospace;
  font-size: 2.2rem;
  font-weight: 200;
  text-align: left;
  margin-left: 1vmin;
  margin-bottom: 1vmin;
}

#search-container > input[type="text"]::selection {
  background-color: tomato;
}

.Welcome {
  border: 1px solid #f9f6f2;

  text-align: center;
}

