.Right > * {
  /* border: 1px solid tomato; */
}
div.LeftRight {
  display: flex;
  height: 100%;
  width: 50%;
  border: 1px solid #f9f6f2;
}
div.RightRight {
  display: flex;
  /* flex-direction: column; */
